.component-main-container{
    height: 500px; 
    width: calc(100% - 1px); 
    overflow: auto; 
    display: flex;
    justify-content: center; 
}

.component-main-contents-container{
    list-style-type: none;
    padding-left: 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 700px;
    width: calc(90% -1px); 
}

.component-el{
    display:flex;
    align-items: center;
}

.inner-contents-container{
    display: flex; 
    align-items: center
}

.img-container-inner-content{
    width: 110px;
    display: flex;
    justify-content: center;
    row-gap: 9px;
}

.inner-content-chart-container{
    width: calc(30% - 1px);
}

.inner-content-chart{
    height: 60px;
    border: solid 1px;
    border-radius: 4px;
}

.outer-chart{
    display: flex;
    justify-content: center;
    height: 100%;
    width: calc(30% - 1px);
    position: sticky;
    top: 0px;
}


/* width */
::-webkit-scrollbar {
    width: 7px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1;
    border-radius: 999px;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 999px;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555;
  }


